'use strict';

/**
* Create a readable stream for generating pseudorandom numbers drawn from a hypergeometric distribution.
*
* @module @stdlib/random-streams-hypergeometric
*
* @example
* import inspectStream from '@stdlib/streams-node-inspect-sink' ;
* import randomStream from '@stdlib/random-streams-hypergeometric' ;
*
* function log( chunk ) {
*    console.log( chunk.toString() );
* }
*
* var opts = {
*     'iter': 10
* };
*
* var stream = randomStream( 5, 3, 2, opts );
*
* stream.pipe( inspectStream( log ) );
*
* @example
* import randomStream from '@stdlib/random-streams-hypergeometric' ;
*
* var opts = {
*     'sep': ',',
*     'objectMode': false,
*     'encoding': 'utf8',
*     'highWaterMark': 64
* };
*
* var createStream = randomStream.factory( opts );
*
* // Create 10 identically configured streams...
* var streams = [];
* var i;
* for ( i = 0; i < 10; i++ ) {
*     streams.push( createStream( 5, 3, 2 ) );
* }
*
* @example
* import inspectStream from '@stdlib/streams-node-inspect-sink' ;
* import randomStream from '@stdlib/random-streams-hypergeometric' ;
*
* function log( v ) {
*    console.log( v );
* }
*
* var opts = {
*     'iter': 10
* };
*
* var stream = randomStream.objectMode( 5, 3, 2, opts );
*
* stream.pipe( inspectStream.objectMode( log ) );
*/


// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import objectMode from './object_mode.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'objectMode', objectMode );
setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
